import axios from 'axios';
import React, { useState, useEffect, } from "react";

export const LogGenerator = () => {

    const accountSID = localStorage.getItem("accountSID");
    const accountAuthToken = localStorage.getItem("accountAuthToken");

    const from = [];
    const earlyDate = {
        year: 2024,
        month: 5,
        day: 1,
    };

    const arrayToCsv = (data) => {
        let csv = "";
        const keys = (data[0] && Object.keys(data[0])) || [];
        csv += keys.join(',') + '\n';
        for (var line of data) {
            csv += keys.map(key => line[key]).join(',') + '\n';
        }
        return(csv);
    }

    const [csvStorage, setCsvStorage] = useState('');
    const [valsStored, setValsStored] = useState(0);
    const [nextPage, setNextPage] = useState('');
    const [continueFlag, setContinueFlag] = useState(true);
    const [disabled, setDisabled] = useState(false);

    function downloadBlob(content, filename, contentType) {
        // Create a blob
        var blob = new Blob([content], { type: contentType });
        var url = URL.createObjectURL(blob);
      
        // Create a link to download it
        var pom = document.createElement('a');
        pom.href = url;
        pom.setAttribute('download', filename);
        pom.click();
    }

    // downloadBlob(csv, 'export.csv', 'text/csv;charset=utf-8;')

    const compareDate = (value) => {
        return(
            value.year > earlyDate.year ||
                (value.year == earlyDate.year &&  (value.month > earlyDate.month || 
                    (value.month == earlyDate.month && value.day > earlyDate.day)
                ))
        );
    }

    const convertDate = (inputDate) => {
        const values = inputDate.split(' ');
        const months = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        return({
            year: values[3],
            month: months.indexOf(values[2]) + 1,
            day: values[1]
        });
    }
    const getNextPage = async(link) => {
        let callURL = ''
        if (link !== '') {
            callURL = `https://api.twilio.com${link}`;
        } else {
            callURL = "https://api.twilio.com/2010-04-01/Accounts/" + accountSID + "/Messages.json?PageSize=10000"
        }
        console.log(callURL);
        await axios.get(
            callURL, {
                auth: {
                    username: accountSID,
                    password: accountAuthToken
                }
            }
        ).then(
            async(res) => {
                if (res.data.messages.length === 1000) {
                    setValsStored((res.data.end + 1) % 1000)
                    console.log('vals: ',valsStored);
                    console.log('res2: ',res.data);
                    if (compareDate(convertDate(res.data.messages[999].date_sent))) {
                        if ((res.data.end + 1) % 1000 !== 0) {
                            let x = await getNextPage(res.data.next_page_uri)
                            return (res.data.messages.concat(x));
                        }
                        console.log('oof');
                        setValsStored(0);
                        setNextPage(res.data.next_page_uri)
                        console.log('hmmm', res.data.messages)
                        const returnVal = res.data.messages;
                        return (returnVal);
                    } else {
                        setContinueFlag(false);
                        return(res.data.messages);
                    }
                } else {
                    setContinueFlag(false);
                    return (res.data.messages);
                }
            }
        )
    }

    const generateLog = async() => {
        setDisabled(true);
        getNextPage(nextPage).then((res) => {
            console.log('res: ',res)
            setCsvStorage(res);
        })
    }

    useEffect(() => {
        if (csvStorage !== '') {
            // downloadBlob(csvStorage, 'export.csv', 'text/csv;charset=utf-8;');
            console.log(csvStorage);
        }
    }, [csvStorage,]);

    return (
        <div>
            <button onClick = {()=> generateLog()} disabled={disabled}>
                Hello World
            </button>
        </div>
    )
}